.blog-preview-container {
    display: flex;
    padding: 50px;
    margin: 20px;
    border: 2px solid #000;
}

.bp-preview-image-container {
    margin: 0 20px;
}

.text-blog-preview-container {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: space-between;
    margin: 0 20px;
    width: 30%;
}

.blog-preview-name {
    margin: 0;
    font-size: 32px;
}

.see-more-blog-btn {
    border: none;
    border-radius: 5px;
    background-color: #B2AE7F;
    width: 280px;
    height: 50px;
    padding: 15px 25px;
    transition: 0.5s;
    font-size: 20px;
}

.see-more-blog-btn:hover {
    font-size: 22px;
    transition: 0.5s;
    background-color: #8a8252;
}